.countCont {
	display: block;
	position: absolute;
	top: 1.5rem;
	left: 2rem;
	z-index: 10;
}

.more {
    position: absolute;
    top: -5px;
    left: 0;
    background-image: url(/react-chess-app/static/media/more.f2acbf2e.svg);
}

.counter {
	display: block;
	margin-top: 5rem;
	line-height: 1.6;
	border-collapse: collapse;
	overflow: hidden;
	display: block;
	transition: 0.3s ease;
	-webkit-transition: 0.3s ease;
	-moz-transition: 0.3s ease;
	-ms-transition: 0.3s ease;
	-o-transition: 0.3s ease;
	background-color: var(--bg-color);
	transform: scale(0);
	z-index: 30;
	-webkit-transform: scale(0);
	-moz-transform: scale(0);
	-ms-transform: scale(0);
	-o-transform: scale(0);
	transform-origin: top left;
	border-radius: 6px;
	opacity: 0;
	-webkit-border-radius: 6px;
	-moz-border-radius: 6px;
	-ms-border-radius: 6px;
	-o-border-radius: 6px;
}

.counter.open-counter {
	transform: none;
	box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
	-webkit-transform: none;
	-moz-transform: none;
	-ms-transform: none;
	-o-transform: none;
	opacity: 1;
}

.counter th {
	border-bottom: 1px solid rgba(134, 134, 134, 0.6);
	text-transform: uppercase;
}

.counter tr {
	width: 20rem;
	padding: 0 5px 0 100px;
}

.counter tr:nth-of-type(even) {
	background-color: rgba(134, 134, 134, 0.15);
}

.counter tr th {
	text-transform: uppercase;
}

.counter th, .counter td {
	padding: 7px 15px;
	text-align: center;
}

/* Buttons */

.colorMode {
	position: absolute;
	top: 1.15rem;
	right: 11rem;
	transition-delay: transform 0.2s;
	z-index: 15;
}

.colorMode.lightMode {
	background-image: url(/react-chess-app/static/media/light_mode.6940108f.svg);
}

.colorMode.darkMode {
	background-image: url(/react-chess-app/static/media/dark_mode.637f3a0a.svg);
}

.setSetting {
	position: absolute;
	top: 1.15rem;
	right: 3rem;
	background-image: url(/react-chess-app/static/media/setting.eaf21f01.svg);
	transition-delay: transform 0.2s;
	z-index: 15;
}

.setSetting:hover, .setSetting:focus, body.open-setting .setSetting {
	cursor: pointer;
	background-color: rgba(128, 128, 128, 0.15);
	opacity: 1;
}

body.open-setting .setSetting {
	transform: rotate(30deg);
	-webkit-transform: rotate(30deg);
	-moz-transform: rotate(30deg);
	-ms-transform: rotate(30deg);
	-o-transform: rotate(30deg);
}

button.closeSetting {
    position: absolute;
    top: 2rem;
    right: 2rem;
    background-image: url(/react-chess-app/static/media/close.244b6687.svg);
}

.reverseOne {
	position: absolute;
	display: block;
	top: 1.15rem;
	right: 7rem;
	background-image: url(/react-chess-app/static/media/undo_black_24dp.70c7fd79.svg);
	z-index: 15;
}

/* Main */

.settings {
	position: fixed;
	top: 0;
	right: 0;
	display: block;
	overflow: auto;
	width: 30rem;
	max-width: 90%;
	height: 100vh;
	padding: 2rem 2.5rem;
	z-index: 20;
	font-size: 1.15rem;
	line-height: 1.6;
	background-color: var(--bg-color);
	box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
	transition: all ease 0.3s;
	transform: translateX(100%);
	-webkit-transform: translateX(100%);
	-moz-transform: translateX(100%);
	-ms-transform: translateX(100%);
	-o-transform: translateX(100%);
	-webkit-transition: all ease 0.3s;
	-moz-transition: all ease 0.3s;
	-ms-transition: all ease 0.3s;
	-o-transition: all ease 0.3s;
}

body.open-setting .settings {
	transform: none;
	-webkit-transform: none;
	-moz-transform: none;
	-ms-transform: none;
	-o-transform: none;
}

.settings h1 {
	margin-bottom: 1rem;
}

.setting {
	position: relative;
	display: flex;
	margin-top: 1rem;
}

/*	Switch settings,
	layout settings,
	mode settings,
	piece style settings,
	time limit settings
*/

.setting.switchSet {
	align-items: center;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}

.setting.extSet,
.setting.modeSet,
.setting.pieceSet,
.setting.timeSet {
	flex-direction: column;
}

.setting.modeSet > div > input,
.setting.pieceSet > div > input,
.setting.timeSet > div > input {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	box-shadow: none;
}

.setting.modeSet > div,
.setting.pieceSet > div,
.setting.timeSet > div {
	display: flex;
	align-items: center;
	justify-content: left;
	padding: 5px 10px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	transition: 0.2s ease;
	-webkit-transition: 0.2s ease;
	-moz-transition: 0.2s ease;
	-ms-transition: 0.2s ease;
	-o-transition: 0.2s ease;
	pointer-events: none;
	z-index: 10 !important;
}

.setting.modeSet > div::before,
.setting.pieceSet > div::before,
.setting.timeSet > div::before {
	content: "";
	display: inline-block;
	width: 1.1rem;
	height: 1rem;
	box-shadow: 0 0 0 2px rgba(128, 128, 128, 0.4);
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	transition: 0.2s ease;
	-webkit-transition: 0.2s ease;
	-moz-transition: 0.2s ease;
	-ms-transition: 0.2s ease;
	-o-transition: 0.2s ease;
}

.setting.modeSet > div > input, .setting.modeSet > div > label,
.setting.pieceSet > div > input, .setting.pieceSet > div > label,
.setting.timeSet > div > input, .setting.timeSet > div > label {
	display: flex;
	align-items: center;
	cursor: pointer;
	pointer-events: auto;
}

.setting.modeSet > div label,
.setting.pieceSet > div label,
.setting.timeSet > div label {
	width: 100%;
}

.setting.modeSet > div:hover,
.setting.pieceSet > div:hover,
.setting.timeSet > div:hover {
	color: var(--primary-color);
	background-color: rgba(104, 111, 128, 0.15);
}
.setting.modeSet > div:hover::before,
.setting.pieceSet > div:hover::before,
.setting.timeSet > div:hover::before {
	box-shadow: 0 0 0 2px var(--primary-lighter-color);
}

.setting.modeSet > div > input::before,
.setting.pieceSet > div > input::before,
.setting.timeSet > div > input::before {
	content: "";
	display: inline-block;
	width: 0.75rem;
	height: 0.75rem;
	margin-top: 0px;
	margin-left: -0.9rem;
	margin-right: 1rem;
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	transform: scale(0);
	-webkit-transform: scale(0);
	-moz-transform: scale(0);
	-ms-transform: scale(0);
	-o-transform: scale(0);
	transition: all 0.2s ease;
	-webkit-transition: all 0.2s ease;
	-moz-transition: all 0.2s ease;
	-ms-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
}

.setting.modeSet > div > input:checked::before,
.setting.pieceSet > div > input:checked::before,
.setting.timeSet > div > input:checked::before {
	background-color: var(--medium-sky-blue);
	transform: scale(1);
	-webkit-transform: scale(1);
	-moz-transform: scale(1);
	-ms-transform: scale(1);
	-o-transform: scale(1);
}

.setting.extSet > span {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-bottom: 5px;
}

.setting.extSet > span::before {
	content: "";
	position: absolute;
	width: 0;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: var(--text-color);
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

.setting.extSet > span:hover:before,
.setting.extSet > span:hover:active,
.setting.extSet > span:hover:focus {
	visibility: visible;
	width: 100%;
}

.setting.extSet > span::after {
	content: "";
	display: inline-block;
	width: 1.5rem;
	height: 1.5rem;
	background-image: url(/react-chess-app/static/media/arrow_forward_ios_black_24dp.18aa13bd.svg);
	background-position: center;
	background-size: 85%;
	background-repeat: no-repeat;
	filter: var(--invert);
	-webkit-filter: var(--invert);
	border-radius: 5px 5px 0 0;
	opacity: 0.85;
	transition: all 0.2s ease;
	-webkit-transition: all 0.2s ease;
	-moz-transition: all 0.2s ease;
	-ms-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	-webkit-border-radius: 5px 5px 0 0;
	-moz-border-radius: 5px 5px 0 0;
	-ms-border-radius: 5px 5px 0 0;
	-o-border-radius: 5px 5px 0 0;
}

.setting.extSet > span:hover {
	/* background-color: rgba(128, 128, 128, 0.1); */
	cursor: pointer;
}

.setting.extSet > span:hover::after {
	opacity: 1;
}

.setting.extSet > span.hist-open::after {
	transform: rotate(90deg);
	-webkit-transform: rotate(90deg);
	-moz-transform: rotate(90deg);
	-ms-transform: rotate(90deg);
	-o-transform: rotate(90deg);
}

.setting.extSet > span.hist-open + .hist-ext {
	display: block;
	max-height: 25rem;
	overflow: auto;
}

.setting.extSet > span:active, .setting.extSet > span:focus {
	/* background-color: rgba(128, 128, 128, 0.2); */
	cursor: pointer;
}

/* History Settings */

.history {
	display: flex;
	flex-direction: column;
	margin-top: 5px;
}

.hist-ext {
	display: none;
}

.history li {
	font-size: 1rem;
	margin-left: 25px;
	padding: 5px 10px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	transition: 0.2s ease;
	-webkit-transition: 0.2s ease;
	-moz-transition: 0.2s ease;
	-ms-transition: 0.2s ease;
	-o-transition: 0.2s ease;
}

.history li:hover {
	color: var(--primary-color);
	background-color: rgba(128, 128, 128, 0.1);
	cursor: pointer;
}

.history li:active, .history li:focus {
	color: var(--primary-color);
	background-color: rgba(128, 128, 128, 0.2);
	cursor: pointer;
}

.custom-time-wrapper {
	display: block !important;
}

.time_input {
	display: flex;
	grid-gap: 5px;
	gap: 5px;
	margin-top: 5px;
}

.time_input input::-webkit-outer-spin-button,
.time_input input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */

.time_input input[type = number] {
	-moz-appearance: textfield;
}

.time_input input {
	width: 2.5rem;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	border: 2px solid rgba(128, 128, 128, 0.3);
	border-radius: 3px;
	outline: 0;
	padding: 3px 5px;
	background-color: transparent;
	color: var(--text-color);
	font-size: 1rem;
	text-align: center;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
}

.time_input input:focus {
	box-shadow: none;
	border-color: var(--medium-sky-blue);
}
.timer {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    width: 20rem;
    max-width: 90%;
    margin: 0 auto;
    padding: 1rem 1.5rem 1.25rem;
    background-color: var(--bg-color);
    border-radius: 0 0 5px 5px;
    -webkit-border-radius: 0 0 5px 5px;
    -moz-border-radius: 0 0 5px 5px;
    -ms-border-radius: 0 0 5px 5px;
    -o-border-radius: 0 0 5px 5px;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.timer .timePanel {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    justify-content: center;
    align-items: center;
    font-size: 1.15rem;
    width: 100%;
}

.timer .timePanelWhite {
    border-right: 1px solid rgba(128, 128, 128, 0.5);
}

.timer .timePanelColor {
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    border: 1px solid rgba(128, 128, 128, 0.5);
}

.timer .timePanelWhite .timePanelColor {
    background-color: white;
}

.timer .timePanelBlack .timePanelColor {
    background-color: black;
}
span.info {
	display: block;
	background-color: var(--light-blue);
	color: var(--medium-sky-blue);
	font-size: 1rem;
	margin: 10px 0;
	padding: 5px 10px 10px 10px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

span.info::before {
	content: "";
	display: block;
	float: left;
	width: 1.75rem;
	height: 1.75rem;
	margin: 2px 8px 0px 0px;
	background-image: url(/react-chess-app/static/media/info.865b0c69.svg);
	background-size: 85%;
	background-position: center;
	background-repeat: no-repeat;
}

.msg {
	position: absolute;
	right: 0;
	bottom: 3rem;
	display: block;
	width: 25rem;
	height: 4rem;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	max-width: 80%;
	background-color: #11111a;
	padding: 1rem 1.25rem;
	border-radius: 10px 0 0 10px;
	-webkit-border-radius: 10px 0 0 10px;
	-moz-border-radius: 10px 0 0 10px;
	-ms-border-radius: 10px 0 0 10px;
	-o-border-radius: 10px 0 0 10px;
	transition: 0.3s ease-in;
	-webkit-transition: 0.3s ease-in;
	-moz-transition: 0.3s ease-in;
	-ms-transition: 0.3s ease-in;
	-o-transition: 0.3s ease-in;
}

.msg::selection {
	background-color: none;
}

.msg span {
	position: relative;
	display: block;
	color: white;
	font-size: 1.15rem;
}

.msg.warning {
	border-left: 5px solid var(--primary-red);
}

.msg.warning span::before {
	content: "WARNING";
	display: block;
	float: left;
	font-weight: bold;
	font-size: 1rem;
	margin-right: 1rem;
	color: var(--primary-red);
	margin-top: 2px;
}

.msg.complete {
	border-left: 5px solid var(--primary-green);
}

.msg.complete span::before {
	content: "COMPLETE";
	display: block;
	float: left;
	font-weight: bold;
	font-size: 1rem;
	margin-right: 1rem;
	color: var(--primary-green);
	margin-top: 2px;
}

.turn-statement {
	position: relative;
	display: block;
	width: 100%;
	height: 5rem;
	background-color: var(--bg-color);
	text-align: center;
	margin: 0rem auto 0rem auto;
	font-size: 1.15rem;
	text-transform: uppercase;
	padding: 1.75rem;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	z-index: 5;
}

.pawn_promo {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    display: block;
    width: 40rem;
    max-width: 90%;
    padding: 1.5rem 2rem;
    border-radius: 5px;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    -o-transform: translateX(-50%) translateY(-50%);
    background-color: var(--body-bg-color);
    text-align: center;
    font-size: 1.15rem;
    line-height: 1.6;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    visibility: hidden;
    opacity: 0;
    z-index: 20;
    transition-delay: opacity 0.2s;
}

body.open-promo .pawn_promo {
    visibility: visible;
    opacity: 1;
}

.promo_opts {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 1.5rem;
    justify-content: space-around;
}

.promo-opt {
    display: block;
    width: 100%;
    height: 10rem;
    padding: 1rem;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.promo-opt::after {
    display: block;
    width: 90%;
    height: 90%;
    font-size: 5rem;
    color: var(--text-color);
    text-align: center;
}

.promo-opt:nth-of-type(1)::after {
    content: "";
}

.promo-opt:nth-of-type(2)::after {
    content: "";
}

.promo-opt:nth-of-type(3)::after {
    content: "";
}

.promo-opt:nth-of-type(4)::after {
    content: "";
}

.promo-opt:hover {
    cursor: pointer;
    background-color: rgba(128, 128, 128, 0.1);
}

.promo-opt:active {
    background-color: rgba(128, 128, 128, 0.15);
}

.promo-opt input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}
.switch {
    position: relative;
    display: inline-block;
    width: 55px;
    height: 17px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(175, 175, 175, 0.3);
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 23px;
    width: 23px;
    top: -2px;
    left: 0px;
    background-color: white;
    transition: .4s;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  }
  
  input:checked + .slider {
    background-color: var(--light-blue);
  }
/*   
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  } */
  
  input:checked + .slider:before {
    background-color: var(--medium-sky-blue);
    transform: translateX(27px);
    -webkit-transform: translateX(27px);
    -ms-transform: translateX(27px);
    -moz-transform: translateX(27px);
    -o-transform: translateX(27px);
}
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
